
* {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
body {
  background: repeat #FF6600;
  font-family: Helvetica, arial, sans-serif;
}
.congrats {
  color: white;
  font-size: 20pt;
  padding: 6px;
  text-align: left;
  color: white;
}
.desc1 {
  font-size: 11pt;
  padding: 6px;
  text-align: center;
  color: white;
}
.desc {
  font-size: 10pt;
  padding: 6px;
  text-align: left;
  color: white;
}
.step {
  font-size: 12pt;
  background: white;
  border: 1px #ccc solid;
  border-radius: 14px;
  padding: 4px 10px;
  margin: 10px 0;
}
.instructions {
  font-size: 12pt;
  font-weight: 600;
}
.icon_image {
  border: 1px #ccc solid;
  border-radius: 10px;
  margin: 0 10px;
}
.button-shortcut {
  font-size: 10pt;
  border: 1px #000 solid;
  border-radius: 5px;
  height: 30px;
  width: 1200px;
  background-color: white;
}
table {
  width: 100%;
}

.tbl-td {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.font-tbl-item {
  font-size: 14px;
  color: "#666666"
}

.tbl-item-wrap {
  display: flex;
  flex-wrap: wrap;
  display: flex;
  width: 120px;
}